
  @import url('https://fonts.googleapis.com/css2?family=Mukta&family=Poppins:wght@300&display=swap');


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Mukta', sans-serif;
    font-family: 'Poppins', sans-serif;
 }

 :root{
    --maincolor:#f5f5f5
 }

.msgcontainer{
    display: flex;
    align-items: center;
    position: fixed;
    bottom: 0px;
    right: 0px;
    width: calc(100% - 25vw);
}

.chatbody{
 
    height: 100vh;
    background-position: center;
    background-repeat: repeat;
    background-attachment: fixed;
    background-color: var(--maincolor);
    /* overflow: auto; */
    padding: 71px 15px 10px 15px;
    position: relative;
}


.chatcont{
    width: 75vw;
    position: relative;
}



.chatcont .overlay{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 500;
    width: 100%;
    height: 100%;
    border-radius: 0px;
    background-color: var(--maincolor);
    display: flex;
    justify-content: center;
    align-items: center;
}

.chatcont .overlay img{
   width: 300px;
   height: 300px;
}


.msgcontainer{
    background-color: var(--maincolor);
    background-color: white;
    padding: 10px;
    box-shadow: 0px -4px 13px -3px rgba(0,0,0,0.2);
}


.msgcontainer button{
    margin-left: 10px;
    display: block;
    border-radius: 100vmax !important;
}

.msgcontainer>div{
    width: 100%;
    max-width: 800px;
    margin: auto;
    display: flex;
    align-items: center;
}

.mainchatcont .fa-microphone{
    margin-left: 10px;
    font-size: 30px;
}

.msgcontainer input{
    height: 39px;
    border: none;
    outline: none;
    width: 100%;
    padding: 10px;
    background-color: #ebebeb;
    color: #3e3e3e;
    font-weight: 600;
}

.chatbody .userchat .msgcont{
    margin-left: auto;
}

.chatbody .userchat .img-cont{
    display: none;
}


.chatbody .userchat .msgcontin{
    background-image: linear-gradient(45deg, #c01583, #8e0ecf);
    transition: all 0.1s;
    color: white !important;
}


.chatbody .userchat .msgcontin p{
    color: white !important;
}



.chatbody .userchat .msgcontin:hover{
    transform: scale(0.9);
}


.chatbody .receiverchat .msgcontin{
    background-color: #e5e5e5;
    color: #000000;
}


.header{
    /* box-shadow: 0px 0px 5px 5px rgb(0 0 0 / 10%);
     */
    z-index: 1;
    position: fixed;
    top: 0px;
    background: white;
    height: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
    padding-left: 10px;
    color: rgb(0, 0, 0);
    border-left: 1px solid white;
    width: calc(100vw - 25vw);
    right: 0px;
    box-shadow: 0px 4px 13px -3px rgba(0,0,0,0.2);
}

.header>div{
    max-width: 800px;
    margin: auto;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header h2{
    padding-left: 10px;
}

.header .userprofile{
display: flex;
align-items: center;
}

.userprofile>div{
    display: flex;
    align-items: center;
}

.mainchatcont{
    display: flex;
}

.msgcontainer .fa-smile{
    font-size: 30px;
    margin-right: 10px;
}

.msgcontainer .fa-paperclip{
    font-size: 30px;
    margin-left: 10px;
}

.msgcontainer aside{
    position: absolute;
    bottom:50px;
}

.mainchatcont>div:nth-child(1){
    width: 25vw;
    position: relative;
    padding-top: 60px;
    box-shadow: 5px 0px 13px -3px rgba(0,0,0,0.2);
}

@media(max-width:768px){
    .mainchatcont>div:nth-child(1){
        position: absolute;
        padding-top: 60px;
        z-index: 5;
        width: 100vw;
        height: 100vh;
        
    }

    .header{
        width: 100vw;
    }

    .chatcont{
        width: 100vw;
        position: fixed;
    }

    .msgcontainer{
        width: 100vw;
    }
}

.mainchatcont .sidebartop{
    display: flex;
    align-items: center;
    background-color:white;
    position: absolute;
    width: 100%;
    left: 0px;
    top: 0px; 
    height: 60px;   
    justify-content: center;
    box-shadow: 0px 4px 13px -3px rgba(0,0,0,0.2);
}


.mainchatcont .sidebartop .userprofile img{
   width: 40px;
   height: 40px;
   border-radius: 50%;
}


.mainchatcont .sidebartop .userprofile {
   display: flex; 
   padding-right: 10px;
 }


.mainchatcont .sidebartop input{
    padding: 8px;
    outline: none;
    border-radius: 10px;
    border: none;
    width: 100%;
    background-color: #ebebeb;
    color: #3e3e3e;
    font-weight: 600;
}

.mainchatcont .usercard{
    background-color: var(--maincolor);
    border-bottom: 1px solid #c7c7c7;
    padding: 10px 15px;
    text-align: left;
    cursor: pointer;
    transition: all 0.3;
    display: flex;
    align-items: center;
    position: relative;
    color: #3e3e3e;
    box-shadow: 0px 4px 13px -3px rgba(0,0,0,0.2);
    border-radius: 10px;
    margin: 10px 15px;
    background: white;
    transition: all 0.3s;
}

.mainchatcont>div{
    background-color:#ebebeb;
}

.mainchatcont .usercard .lastchattime small{
    position: absolute;
    right: 15px;
    top: 13px;
}

.mainchatcont .usercard img{
    margin-right: 10px;
}

.mainchatcont .usercard .profile{
  display: flex;
}


.mainchatcont .usercard:hover{
    background-color: #3e3e3e; 
    color: white; 
    transform: scale(0.98);  
}


.mainchatcont .sidebartop{
    padding: 5px 15px;
}
.sidebartop .searchbar{
    width: 100%;
    
}


.chatbodyin{
    max-width: 800px;
    margin: auto;
    max-height: calc(100% - 6vh);
    overflow: auto;
}

.chatbodyin::-webkit-scrollbar{
    display: none;
}

.css-sghohy-MuiButtonBase-root-MuiButton-root.Mui-disabled {
    color: rgb(255 255 255) !important;
    box-shadow: none;
    background-color: #8e0ecf !important;
    border-radius: 100vmax !important;
    padding: 7px !important;
}

.css-sghohy-MuiButtonBase-root-MuiButton-root{
    color: rgb(255 255 255) !important;
    box-shadow: none;
    background-color: #8e0ecf !important;
    border-radius: 100vmax !important;
    padding: 7px !important;
}

.activemem{
position: absolute;
right: 0px;
}